.wrapDesktopNavigation {
  display: flex;
  justify-content: center;
  flex-direction: row;
  margin: 0 1rem;
  font-family: "Helvetica Neue";
}

.wrapMobileNavigation {
  display: flex;
  justify-content: center;
  position: absolute;
  left: -100vw;
  top: 4rem;
  transition: all 400ms ease-in-out;
  width: 40%;
  height: 100vh;
  background: #47423e url(../../../img/navTexture.png) repeat 0 0;
}

.activeMobileNav {
  top: 4rem;
  left: 0;
  transition: all 400ms ease-in-out;
}

.desktopNavBar {
  display: flex;
  list-style-type: none;
  padding: 0;
}

.mobileNavBar {
  list-style-type: none;
  padding: 0;
  height: 100%;
  margin: 1rem auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.desktopNavBarItems {
  margin: auto 1rem;
  /* color: #454545; */
  color: white;
  text-decoration: none;
}

.mobileNavBarItems {
  margin: 1rem auto;
  color: white;
  font-size: large;
  width: 100%;
  height: 2rem;
  text-decoration: none;
}

.desktopNavBarItems:hover,
.mobileNavBarItems:hover {
  color: #1272ce;
  cursor: pointer;
}
