.wrapServiceList {
  position: absolute;
  display: flex;
  flex-direction: column;
  margin-top: 10rem;
  padding: 0;
  width: 0;
  list-style: none;
  z-index: 100;
}

.serviceLabel {
  display: block;
  position: relative;
  margin: 0.65em 0 0.65em 1.1em;
  cursor: pointer;
}

.servicesItem {
  margin: 0.65em 0 0.65em 1.1em;
}

.servicesListBtn {
  opacity: 0;
  overflow: hidden;
  cursor: pointer;
}

.servicesListBtnChecked {
  position: absolute;
  padding: 0.65em 4em 0.65em 1.1em;
  font-size: 1.6rem;
}

.servicesListBtn:checked ~ .servicesListBtnChecked {
  position: absolute;
  width: 170px;
  color: white;
  border-radius: 5px;
  background-color: #312d2a;
  transition: background-color 0.3s ease, color 0.3s ease;
}

@media (min-width: 1600px) {
  .wrapServiceList {
    padding: 0 4rem;
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
  .servicesItem {
    margin: 0.45em 2em 0.45em 0.6em;
  }
  .servicesListBtnChecked {
    padding: 0.45em 2em 0.45em 0.6em;
    font-size: 1.5rem;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .servicesItem {
    margin: 0.35em 2em 0.35em 0.6em;
  }
  .servicesListBtnChecked {
    padding: 0.35em 2em 0.35em 0.6em;
    font-size: 1.4rem;
  }
}
