.wrapMobileServicesInfo {
  display: flex;
  flex-direction: row;
  margin-top: 9rem;
  max-width: 95vw;
  overflow-x: auto;
}

.mobileServicesInfoCard {
  height: 50vh;
  width: 95vw;
  margin-right: 1rem;
  background-color: #f1efed;
  border-radius: 0 0 5px 5px;
}

.mobileServicesInfoHeader {
  border-radius: 5px 5px 0 0;
  background-color: #312d2a;
  color: white;
  padding: 0.8em 20px;
}

.mobileServicesInfoHeader h2 {
    margin: 0;
}

.mobileServicesInfoBody {
    width: 85vw;
    list-style: none;
    padding: 0 1rem;
}

::-webkit-scrollbar { 
    display: none; 
}