.wrapHeader {
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 7rem;
  position: fixed;
  top: 0;
  width: 100%;
}

.wrapLogo {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  background-color: #fff;
  height: 3rem;
  padding: 0 1rem;
}

.wrapPageName {
  display: flex;
  align-items: center;
  height: 4rem;
  background-color: #8d909b;
  padding: 0 2rem;
  color: white;
  font-family: "Helvetica Neue Ultra Light", Times, serif;
  font-weight: 500;
  font-size: 2rem;
}

@media (max-width: 767px) {
  .wrapLogo {
    justify-content: center;
    position: absolute;
    top: 0;
    width: 100%;
  }
  .wrapPageName {
    height: 15px;
    font-size: 0.8rem;
    font-weight: 900;
    align-items: flex-end;
  }
}
