.pluginImg {
  width: 110%;
  max-width: 70%;
  position: relative;
  height: 0;
  padding: 37% 0 0 0;
  margin: 50px 0 60px 0;
  background-size: cover;
  background-image: url("../../../../../img/imgPlugin.png");
  background-repeat: no-repeat;
}

@media (max-width: 767px) {
  .pluginImg {
    background-position: center;
  }
}
