.wrapLogo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 0 1rem;
  cursor: pointer;
}

.anonimosLogoGray {
  color: #e8e7e5;
  font-family: "Prometheanexpand", Times, serif;
  font-size: 40px;
}

.anonimosLogoBlack {
  color: black;
  font-family: "Prometheanexpand", Times, serif;
  font-size: 40px;
}

.developmentLogo {
  color: gray;
  font-family: "Helvetica Neue", Times, serif;
  font-size: 18px;
}

@media (max-width: 767px) {
  .anonimosLogoGray,
  .anonimosLogoBlack {
    font-size: 30px;
    margin-bottom: 5px;
  }

  .logoContainer {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }
}
