.hamburgerBtn,
.hamburgerBtn::before,
.hamburgerBtn::after {
  width: 25px;
  height: 3px;
  background-color: #c4c4c4;
  cursor: pointer;
  transition: all 400ms ease-in-out;
  z-index: -1;
}

.wrapHamburger {
  display: flex;
  align-items: center;
  position: absolute;
  left: 0;
  width: 25px;
  height: 100%;
  cursor: pointer;
  margin-left: 1rem;
  z-index: 10000;
}

.hamburgerBtn::before {
  position: relative;
  display: block;
  content: "";
  top: -7px;
  cursor: pointer;
  z-index: -1;
}

.hamburgerBtn::after {
  position: relative;
  display: block;
  content: "";
  top: 4px;
  cursor: pointer;
  z-index: -1;
}

.hamburgerBtnIsActive {
  /* -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  -o-transform: rotate(90deg);
  transform: rotate(90deg); */
  width: 15px;
  transition: all 400ms ease-in-out;
}
