.wrapForm {
  display: flex;
  justify-content: center;
  align-items: center;
}

.formBody {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.formInputFiled {
  width: 350px;
  height: 40px;
  border-radius: 5px;
  outline: 0;
  border: 1px solid #8a8a89;
  padding: 2px 0.5rem;
  color: #00698c;
  margin: 1rem auto;
}

.formInputLabel {
  position: absolute;
  color: #8a8a89;
  font-size: large;
  cursor: auto;
  pointer-events: none;
  padding: 1.8rem 0.5rem;
  transition: padding 350ms;
}

.formInputFiled:focus {
  border: 1px solid #00698c;
}

.formInputFiled:focus + .formInputLabel {
  color: #00698c;
  font-size: small;
  padding: 1.2rem 0.5rem;
}

.activeField {
  color: #00698c;
  font-size: small;
  padding: 1.2rem 0.5rem;
}

.formMessageField {
  width: 350px;
  height: 200px;
  border-radius: 5px;
  resize: none;
  outline: 0;
  border: 1px solid #8a8a89;
  padding: 1rem 0.5rem;
  color: #00698c;
}

.formTextLabel {
  position: absolute;
  color: #8a8a89;
  font-size: large;
  cursor: auto;
  padding: 0.7rem 0.5rem;
  transition: padding 350ms;
  pointer-events: none;
}

.formMessageField:focus {
  border: 1px solid #00698c;
}

.formMessageField:focus + .formTextLabel {
  color: #00698c;
  font-size: small;
  padding: 0.2rem 0.5rem;
}

.activeTextField {
  color: #00698c;
  font-size: small;
  padding: 0.2rem 0.5rem;
}

.formSendBtn {
  width: 65px;
  height: 34px;
  background-color: #312d2a;
  border-radius: 5px;
  color: white;
  outline: 0;
  border: 0;
  margin: 1rem;
}

.formSendBtn:active {
  background-color: #312d2aa6;
}

.wrapInputField {
  display: grid;
  justify-content: center;
}
