.wrapHeader {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 4rem;
  background: #34302e;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.05);
  position: fixed;
  top: 0;
}

.headerTexture {
  position: fixed;
  left: 0;
  top: 4rem;
  background-color: transparent;
  background: transparent url(../../img/headerTexture.png) center center no-repeat;
  width: 100%;
  height: 12px;
  min-height: 12px;
  background-size: cover;
}

.linkStyle{
  text-decoration: none;
}

@media (max-width: 767px) {
  .wrapHeader {
    justify-content: center;
  }
}
