.wrapInfoBox {
  display: flex;
  margin-top: 10rem;
  border-radius: 10px;
  background-color: #f1efed;
  width: 80vw;
  z-index: 100;
}

.infoBox {
  list-style: none;
  padding: 0;
  margin: 45px 45px 45px 115px;
}

.infoBoxArticle {
  margin: 0.5rem auto;
}

@media (max-width: 767px) {
  .wrapInfoBox {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
